input, button {
  border-radius: 9px;
  margin: 10px 5px 20px 7px;
}

.timer {
  background-image: linear-gradient(to right, rgba(0, 255, 110, 0), #00ff37);
  border: 2px solid;
  border-radius: 50px;
  justify-content: space-evenly;
  font-family: Courier New, monospace;
  display: flex;
}

.field {
  flex-direction: column-reverse;
  justify-content: center;
  align-items: center;
  padding: 20px;
  display: flex;
}

.label {
  color: #000;
  font-size: 18px;
  font-weight: 600;
}

.value {
  color: #000;
  font-size: 36px;
  font-weight: 700;
}

